import './App.css';
import Box from './Components/Box';
import Form from './Components/Form';
import Msg from './Components/Msg';
import img from './Assets/Group 2.svg';
import ss from './Assets/Frame 6.png';
import { ReactMic } from 'react-mic';
import { useEffect, useState } from 'react';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import Modal from './Components/Modal';
function App() {
	const onData = (recordedBlob) => {};
	const [scam, setScam] = useState(false);
	const onStop = (recordedBlob) => {};
	const [rec, setRec] = useState(false);
	const [open, setOpen] = useState(false);
	const [scamno, setScamno] = useState(0);
	const escFunction = (event) => {
		if (event.keyCode === 113) {
			console.log(event.keyCode);
			setOpen(true);
		}
	};
	useEffect(() => {
		document.addEventListener('keydown', escFunction, false);
		return () => {
			document.removeEventListener('keydown', escFunction, false);
		};
	}, []);
	return (
		<Router>
			<Switch>
				<div className='App'>
					<Route path='/submit' exact>
						<>
							<img
								src={ss}
								style={{
									width: '100vw',
									height: '100vh',
									position: 'fixed',
								}}
							/>
						</>
					</Route>
					<Route path='/' exact>
						<>
							<Modal open={open} setOpen={setOpen} />
							<div
								style={{
									height: '15%',
									width: '100%',
									display: 'flex',
									justifyContent: 'center',
									alignItems: 'center',
								}}>
								<div className='as'>INDIAN RAILWAYS</div>
							</div>

							<Box>
								<Form scam={rec} scamno={scamno} />
								<Msg />
							</Box>
							<div
								style={{
									position: 'fixed',
									bottom: '5%',
									right: '5%',
									width: '30vw',
									display: 'flex',
								}}>
								<ReactMic
									record={rec}
									className={`sound-wave ${rec ? '' : 'ded'}`}
									onStop={onStop}
									onData={onData}
									strokeColor='#ffffff'
									backgroundColor='#000000'
								/>
								<img
									src={img}
									style={{ cursor: 'pointer' }}
									onClick={() => {
										setRec((prev) => !prev);
										setScamno((prev) => prev + 1);
									}}
								/>
							</div>
						</>
					</Route>
				</div>
			</Switch>
		</Router>
	);
}

export default App;
